import React from "react";
import { CopyToClipboard } from "react-copy-to-clipboard";
import { Phone, PhoneDisabled, Assignment } from "@material-ui/icons";
import { TextField } from "@material-ui/core";

import { useContextValue } from "../../../SocketContext";
import { useContextLogin } from "../../../LoginContext";

const Optional = ({ children }) => {
  const { toUser, setToUser, inCall, callUser, hangup } = useContextValue();

  const { account } = useContextLogin();

  const handleSubmit1 = (e) => {
    e.preventDefault();
  };
  const handleSubmit2 = (e) => {
    e.preventDefault();
    if (toUser.length < 3) {
      return;
    }
    if (inCall) {
      hangup();
    } else {
      callUser();
    }
  };

  return (
    <div className="mt-3 col-md-12">
      <div className="mb-2">{children}</div>
      <div className="row">
        <div className="col-md-6 col-sm-6 col-6 p-3">
          {/* set name, get id */}
          <form onSubmit={handleSubmit1} className="bg-light p-3 rounded">
            <h4 className=" mb-3">Account Info</h4>

            <TextField label="Name" fullWidth></TextField>

            <CopyToClipboard text={account}>
              <button type="button" className="w-100 btn btn-primary mt-3">
                Copy name <Assignment fontSize="large" />
              </button>
            </CopyToClipboard>
          </form>
        </div>

        {/* another notification */}

        <div className="col-md-6 col-sm-6 col-6 p-3">
          {/* set name, get id */}
          <form onSubmit={handleSubmit2} className="bg-light p-3 rounded">
            <h4 className="mb-3">Make a call</h4>
            <TextField
              label="ID to call"
              value={toUser}
              onChange={(e) => setToUser(e.target.value)}
              fullWidth
            ></TextField>
            {inCall ? (
              <button
                type="button"
                className="w-100 btn btn-danger mt-3"
                onClick={hangup}
              >
                Hang up <PhoneDisabled fontSize="large" />
              </button>
            ) : (
              <button
                type="button"
                className="w-100 btn btn-success mt-3"
                onClick={callUser}
              >
                Call <Phone fontSize="large" />
              </button>
            )}
          </form>
        </div>
        {/*  */}
      </div>
    </div>
  );
};

export default Optional;
