import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";

import { useContextLogin } from "../../LoginContext";

import ChatOne from "./chatnormal/ChatOne";
import ChatGroup from "./chatnormal/ChatGroup";
import ChatVideoOne from "./chatVideoOne/ChatVideoOne";
import VideoPlayerGroup from "./chatVideoGroup/VideoPlayerGroup";

const Profile = () => {
  let history = useHistory();

  const [fullAcc, setFulAcc] = useState([]);

  const [typeActive, setTypeActive] = useState({
    chatOne: true,
    chatGroup: false,
    chatVideoOne: false,
    chatVideoGroup: false,
  });

  const { isLogin, account } = useContextLogin();

  const changeType = (e) => {
    if (e === "chatOne") {
      setTypeActive({
        ...typeActive,
        chatOne: true,
        chatGroup: false,
        chatVideoOne: false,
        chatVideoGroup: false,
      });
    } else if (e === "chatVideoOne") {
      setTypeActive({
        ...typeActive,
        chatOne: false,
        chatGroup: false,
        chatVideoOne: true,
        chatVideoGroup: false,
      });
    } else return;
  };

  const getFullAcc = async () => {
    // let link = "http://127.0.0.1:5005/api/getFullAcc";
    let link = "https://chat-video-react-v1.herokuapp.com/api/getFullAcc";
    
    // let link = "http://127.0.0.1:8000/api/getFullAcc/";
    // let link = "https://learn-web-django01.herokuapp.com/api/getFullAcc/";

    await fetch(link, {
      method: "POST",
      headers: {
        "Access-Control-Allow-Origin": "*",
        "Content-Type": "application/json"
      },
      body: JSON.stringify({ username: account }),
    })
      .then((res) => res.text())
      .then((res) => {
        let rs = JSON.parse(res);
        setFulAcc(rs);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  useEffect(() => {
    if (!isLogin) {
      history.replace("/login");
    } else {
      getFullAcc();
    }
  }, []);

  return (
    <>
      <div className="row mt-5">
        <div className="col-md-8">
          {!typeActive["chatVideoOne"] ? (
            <div className="d-flex justify-content-between">
              <h1>User name: {account}</h1>
              <button
                onClick={() => history.push("/")}
                className="btn btn-outline-danger"
              >
                Back home
              </button>
            </div>
          ) : (
            <>
              <div className="text-center">
                <h1>Chat video (Hiện Cò)</h1>
              </div>
            </>
          )}
          <div className="mt-2">
            {typeActive["chatOne"] && <ChatOne></ChatOne>}
            {typeActive["chatGroup"] && <ChatGroup></ChatGroup>}
            {typeActive["chatVideoOne"] && <ChatVideoOne></ChatVideoOne>}
            {typeActive["chatVideoGroup"] && (
              <VideoPlayerGroup></VideoPlayerGroup>
            )}
          </div>
        </div>
        <div className="col-md-4">
          <div className="list-group">
            <button
              type="button"
              className={
                typeActive["chatOne"]
                  ? "list-group-item list-group-item-action active"
                  : "list-group-item list-group-item-action"
              }
              onClick={() => changeType("chatOne")}
            >
              Chat one
            </button>
            <button
              type="button"
              className="list-group-item list-group-item-action"
              disabled
            >
              Chat group
            </button>
            <button
              type="button"
              className={
                typeActive["chatVideoOne"]
                  ? "list-group-item list-group-item-action active"
                  : "list-group-item list-group-item-action"
              }
              onClick={() => changeType("chatVideoOne")}
            >
              Chat video one
            </button>
            <button
              type="button"
              className="list-group-item list-group-item-action"
              disabled
            >
              Chat video group
            </button>
          </div>

          {/* load user */}
          <div className="list-group mt-5">
            <h4>List user</h4>
            {fullAcc.map((item, index) => {
              return (
                <button
                  key={index}
                  type="button"
                  className="list-group-item list-group-item-action"
                >
                  {item["username"]}
                  <span className="mx-5">offline</span>
                </button>
              );
            })}
          </div>
        </div>
      </div>
    </>
  );
};

export default Profile;
