import React, { useEffect, useState } from "react";
import MicIcon from "@material-ui/icons/Mic";
import MicOffIcon from "@material-ui/icons/MicOff";
import VideocamOffIcon from "@material-ui/icons/VideocamOff";
import VideocamIcon from "@material-ui/icons/Videocam";

import { useContextValue } from "../../../SocketContext";
import { useContextLogin } from "../../../LoginContext";

const VideoPlayer = () => {
  const {
    inCall,
    fromUser,
    myVideo,
    toUser,
    userVideo,
    stream,
    turnOffCam,
    turnOffMic,
    // testCase,
  } = useContextValue();

  // const [size, setSize] = useState(window.innerWidth);
  // console.log(size);

  const { account } = useContextLogin();

  const [onMic, setOnMic] = useState(true);
  const [onCam, setOnCam] = useState(true);

  const [changeCol, setChangeCol] = useState("mx-auto col-md-8 col-12 p-3");

  useEffect(() => {
    if (inCall) {
      setChangeCol("mx-auto col-md-6 col-12 p-3");
    }
  }, [inCall]);

  // useEffect(()=>{
  //   console.log('changer router');
  // })

  return (
    <>
      <div className="col-md-12">
        <div className="row">
          <div className="my-3 d-flex flex-column ">
            <h2>user name: {account}</h2>
            <div className="d-flex justify-content-around">
              <button
                className="btn btn-outline-danger "
                onClick={() => {
                  turnOffCam();
                  setOnCam(!onCam);
                }}
              >
                {onCam ? (
                  <>
                    Off camera <VideocamOffIcon />
                  </>
                ) : (
                  <>
                    On camera <VideocamIcon />
                  </>
                )}
              </button>
              <button
                className="btn btn-outline-danger "
                onClick={() => {
                  turnOffMic();
                  setOnMic(!onMic);
                }}
              >
                {onMic ? (
                  <>
                    Off micro <MicOffIcon />
                  </>
                ) : (
                  <>
                    On micro <MicIcon />
                  </>
                )}
              </button>
              {/* <button className="btn btn-outline-danger " onClick={testCase}>
                Test Case
              </button> */}
            </div>
          </div>

          {/* our own video */}
          <div className='d-flex flex-column'>
            {stream && (
              <div className={changeCol}>
                <video
                  className="w-100 border border-success"
                  // style={{ width: `${size}` }}
                  ref={myVideo}
                  playsInline
                  autoPlay
                  muted
                />
              </div>
            )}

            {/* our own video */}
            {inCall && (
              <div className={changeCol}>
                <video
                  className="w-100 border border-success"
                  ref={userVideo}
                  playsInline
                  autoPlay
                />

                <div className="mt-3">
                  <h4>calling: {fromUser || toUser}</h4>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default VideoPlayer;
