import React, { useEffect, useRef } from "react";
import { Link, useLocation, useHistory } from "react-router-dom";

import { useContextLogin } from "../../LoginContext";

import logo from "../asset/logo192.png";

const Header = () => {
  const { isLogin, setIsLogin } = useContextLogin();

  const local = useLocation();
  let history = useHistory();

  const refContainer = useRef(null);

  useEffect(() => {
    if (local.pathname === "/profile") {
      refContainer.current.style.display = "none";
    } else {
      refContainer.current.style.display = "";
    }

    if (isLogin && local.pathname === "/login") {
      history.replace("/profile");
    }
  }, [local.pathname]);

  const handleSubmit = (e) => {
    e.preventDefault();
    alert("helloooo");
  };

  const logout = () => {
    let makeSure = window.confirm("Are you sure?");
    if (makeSure) {
      setIsLogin(false);
    }
  };

  return (
    <>
      <nav
        ref={refContainer}
        className="navbar navbar-expand-lg navbar-light bg-light px-3"
      >
        <div className="container-fluid">
          
          <button
            className="navbar-toggler mb-3"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <img
                  src={logo}
                  alt="logo"
                  style={{ width: "40px", marginRight: "10px" }}
                />
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/">
                  Home
                </Link>
              </li>
              <li className="nav-item dropdown">
                <Link className="nav-link" to="/about">
                  About
                </Link>
              </li>

              {isLogin && (
                <li className="nav-item dropdown">
                  <Link className="nav-link" to="/profile">
                    Profile
                  </Link>
                </li>
              )}
              {isLogin && local.pathname !== "/login" && (
                <li className="nav-item">
                  <Link className="nav-link" href="#" onClick={logout}>
                    Log out
                  </Link>
                </li>
              )}

              {!isLogin && local.pathname !== "/login" && (
                <li className="nav-item">
                  <Link className="nav-link" to="/login">
                    Login
                  </Link>
                </li>
              )}
            </ul>
            <form onSubmit={handleSubmit} className="d-flex">
              <input
                className="form-control me-2"
                type="search"
                placeholder="Search"
                aria-label="Search"
              />
              <button className="btn btn-outline-success" type="submit">
                Search
              </button>
            </form>
          </div>
        </div>
      </nav>
    </>
  );
};

export default Header;
