import React, { useState } from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";

import { useHistory } from "react-router-dom";

import { useContextLogin } from "../../LoginContext";

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function SignIn() {
  const { setIsLogin, setAccount } = useContextLogin();
  let history = useHistory();

  const [formData, setFormData] = useState({ username: "", password: "" });
  const [error, setError] = useState("");

  const classes = useStyles();

  const handleSubmit = async (e) => {
    e.preventDefault();
    // let link = "http://127.0.0.1:5005/api";
    let link = "https://chat-video-react-v1.herokuapp.com/api";
    
    // let link = "http://127.0.0.1:8000/api/";
    // let link = "https://learn-web-django01.herokuapp.com/api/";

    await fetch(link, {
      method: "POST",
      headers: {
        "Access-Control-Allow-Origin": "*",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        username: formData["username"].trim(),
        password: formData["password"].trim(),
      }),
    })
      .then((res) => {
        // console.log(res);
        if (res.status === 403) {
          setError("the account do not exist");
        } else if (res.status === 401) {
          setError("the incorrect password");
        } else if (res.status === 200) {
          setIsLogin(true);
          setAccount(formData["username"]);
          history.push("/profile");
        }
      })
      .catch((err) => {
        console.log(err.toString());
        // setError(err);
      });
  };

  const changValue = (e) => {
    const name = e.target.name;
    const value = e.target.value;

    setFormData({ ...formData, [name]: value });
  };

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Log in
        </Typography>

        <span className="text-danger mt-2">{error}</span>

        <form onSubmit={handleSubmit} className={classes.form}>
          <TextField
            margin="normal"
            required
            fullWidth
            label="User name"
            name="username"
            autoFocus
            value={formData.username}
            onChange={changValue}
          />
          <TextField
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            value={formData.password}
            onChange={changValue}
          />

          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Log In
          </Button>
          <Grid container>
            <Grid item>
              <Link variant="body2">{"Don't have an account? Register"}</Link>
            </Grid>
          </Grid>
        </form>
      </div>
    </Container>
  );
}
