import React from "react";

import { useContextValue } from "../../../SocketContext";

const Notifications = () => {
  const { declineText, notFound, showBtnAnswer, fromUser, answer, decline } =
    useContextValue();

  return (
    <div className="mb-5">
      {declineText && (
        <div className="text-center alert alert-danger">{declineText}</div>
      )}
      {notFound && (
        <div className="text-center alert alert-warning">{notFound}</div>
      )}

      {showBtnAnswer && (
        <div className="d-flex flex-column text-center">
          <h3>{fromUser} is calling</h3>
          <div className="d-flex justify-content-around">
            <button className="btn btn-success" onClick={answer}>
              Answer
            </button>
            <button className="btn btn-danger" onClick={decline}>
              Decline
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default Notifications;
